.item {
  margin: 10px;
  width: 300px;
  background-color: honeydew;
  display: flex;
}

.status {
  width: 20px;
  height: 20px;
  margin-top: 50px;
  margin-left: 10px;
  background-color: blueviolet;
  border-radius: 10px;
}

.avatar {
  margin: 5px 10px;
  background-color: cadetblue;
  border-radius: 5px;
}

.name {
  font-size: 34px;
  font-weight: 500;
}
